import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  tap = 0;
  swipe = 0;
  swipeLeft = 0;
  swipeRight = 0;
  swipeEvent(event){
    console.log(event);
    if (event.deltaX < 0){
      this.swipeLeft++;
    }else{
      this.swipeRight++;
    }
    this.swipe++;
  }
  tapEvent(event){
    this.tap++;
    console.log(event);
  }
  notify(){

  }
  constructor(public navCtrl: NavController) {

  }

}



